import React, { useState } from "react";
import {Accordion, AccordionSummary, AccordionDetails, Box, ToggleButton, ToggleButtonGroup, Typography } from '@mui/material';

import GlossaryTerm from '../GlossaryTerm';
import ResiDefinitionChart from "../ResiDefinitionChart";
import MedianExpenditureChart from "../MedianExpenditureChart";
import QuintilExpenditureCharts from "../QuintilExpenditureCharts";
import HouseholdTable from "../HouseholdTable";
import CounterAnimation from "../CounterAnimation";
import MeanMapChart from "../MeanMapChart";
import EducationCharts from "../EducationCharts";
import ExpenditureBundTable from "../ExpenditureBundTable";

import ExpandMoreIcon from "@mui/icons-material/ExpandMore";


const ResidualIncomeSection = () => {

    const [isWrapped, setIsWrapped] = useState(false);

    const [medianExpenditureMode, setMedianExpenditureMode] = useState("all");
    const [meanMapMode, setMeanMapMode] = useState("all");
    const [quintilExpenditureMode, setQuintilExpenditureMode] = useState("absolute");
    
    const handleToggleModeChange = (type, newValue) => {
        if (newValue !== null) {
            if (type === "median_exp") {
                setMedianExpenditureMode(newValue);
            } else if (type === "quintil_exp") {
                setQuintilExpenditureMode(newValue);
            } else if (type === "mean_map") {
                setMeanMapMode(newValue);
            } 
        }
    };

    return (
        <div>
            <h2 className="section-header">Residualeinkommen in sterreich</h2>
            <p className='paragraph'>
                Whrend das Haushaltseinkommen ein wichtiger Indikator fr wirtschaftliche Verhltnisse ist, stellt sich die Frage, wie viel davon nach den grundlegenden Lebenshaltungskosten im Monat brigbleibt. Das sogenannte <GlossaryTerm className="glossary-term residualIncome" sectionId="residualIncome">Residualeinkommen</GlossaryTerm>  also das Einkommen nach den Ausgaben fr Essen, Wohnen, Bildung, Gesundheit und Mobilitt  gibt einen noch genaueren Einblick in die finanzielle Lage der Haushalte. Es zeigt, wie viel finanzieller Spielraum tatschlich fr individuelle Bedrfnisse, Ersparnisse oder Freizeitaktivitten zur Verfgung steht. Wie man hier sehen kann, lsst sich das <GlossaryTerm className="glossary-term residualIncome" sectionId="residualIncome">Residualeinkommen</GlossaryTerm> berechnen, indem man vom Einkommen der Haushalte, <GlossaryTerm className="glossary-term expenditure" sectionId="residualIncome">notwendige Ausgaben</GlossaryTerm> abzieht. 
            </p>
        
            <Box 
                className="definition-chart-container"
                sx={{
                    // background: "#f4f4f4",
                    // boxShadow: 1,
                    padding: 0,
                    borderRadius: 1,
                    marginTop: 3,
                    marginBottom: 3
                }}
            >
                <ResiDefinitionChart />
            </Box>

            <div style={{margin: '24px 0 24px'}}>
                <Accordion sx={{background: "#f4f4f4"}}>
                    <AccordionSummary expandIcon={<ExpandMoreIcon />}>
                        <Typography variant="h4" sx={{ fontSize: '1.125rem'}}>Wo kommen unsere Daten her und wie funktioniert die Berechnung genau?</Typography>
                    </AccordionSummary>
                    <AccordionDetails>
                        <Typography>Zur Berechnung der verfgbaren Einkommen werden die Abgestimmte Lohn- und Einkommensteuerstatistik 2019 und 2020 und die <GlossaryTerm className="glossary-term consumption" sectionId="residualIncome">Konsumerhebung</GlossaryTerm> (2019-2020) verwendet. Die Informationen zu Personen knnen ber eine ID zwischen den beiden Erhebungen verknpft und Personen eindeutig Haushalten zugeordnet werden. Das an die OECD-Skala angepasste Haushaltseinkommen wird berechnet. Die <GlossaryTerm className="glossary-term consumption" sectionId="residualIncome">Konsumerhebung</GlossaryTerm> liefert detaillierte Informationen ber die Ausgaben der Haushalte, wobei die unter <GlossaryTerm className="glossary-term expenditure" sectionId="residualIncome">notwendige Ausgaben</GlossaryTerm> zusammengefassten Kategorien identifiziert werden knnen. Whrend fr alle Personen in sterreich mit steuerrelevantem Einkommen Informationen vorliegen, handelt es sich bei der <GlossaryTerm className="glossary-term consumption" sectionId="residualIncome">Konsumerhebung</GlossaryTerm> um eine reprsentative Stichprobenerhebung, die 6.873 Haushalte umfasst. Das verfgbare Einkommen kann daher nur fr diese Haushalte berechnet werden, weshalb die Ergebnisse auf der Ebene von Personengruppen, Regionen und Haushaltstypen ausgewertet werden.</Typography>
                    </AccordionDetails>
                </Accordion>
            </div>

            <h2 className="content-header">Wofr geben Haushalte Geld aus?</h2>
            <p className='paragraph'>
                Betrachtet man alle Haushalte gemeinsam, zeigt sich, dass im Durchschnitt etwa 10% des Einkommens fr Wohnen aufgewendet werden. Ein hnlicher Anteil entfllt auf Ernhrung. Doch ein genauerer Blick auf Mieter:innenhaushalte offenbart eine deutliche finanzielle Belastung: Hier steigt der Anteil der Wohnkosten drastisch auf ber 23% des Einkommens. Diese Differenz wirkt sich auch stark auf das <GlossaryTerm className="glossary-term residualIncome" sectionId="residualIncome">Residualeinkommen</GlossaryTerm> aus  also das Einkommen, das nach Grundausgaben fr Wohnen, Ernhrung, Bildung, Gesundheit und Mobilitt verbleibt. Whrend der Median des <GlossaryTerm className="glossary-term residualIncome" sectionId="residualIncome">Residualeinkommens</GlossaryTerm> ber alle Haushalte hinweg bei etwa 70% des Haushaltseinkommens liegt, sinkt dieser Wert fr Mieter:innenhaushalte auf nur noch ca. 58 %.Das bedeutet, dass Mieter:innen deutlich weniger finanziellen Spielraum fr weitere Ausgaben oder Rcklagen haben.
            </p>

            <Box
                sx={{
                    background: "#f4f4f4",
                    padding: 2,
                    borderRadius: 1,
                    boxShadow: 1,
                    marginTop: 3,
                    marginBottom: 3,
                }}
            >
                <div className="toggle-container">
                    <ToggleButtonGroup
                        className="residual-income-toggle median-exp-toggle"
                        value={medianExpenditureMode}
                        exclusive
                        onChange={(_, newValue) => handleToggleModeChange("median_exp", newValue)}
                        aria-label="median expenditure chart mode selection"
                    >
                        <ToggleButton value="all"><label>Allen</label></ToggleButton>
                        <ToggleButton value="renter"><label>Mieter:Innen</label></ToggleButton>
                    </ToggleButtonGroup>
                </div>

                <h3 className="median-exp-chart-title">Ausgabenkategorien</h3> 
        
                <div className="median-exp-chart-container">
                    <MedianExpenditureChart mode={medianExpenditureMode} />
                </div>
            </Box>



            <h2 className="content-header">Residualeinkommen nach Haushaltstyp</h2>
            <p className='paragraph'>
                Wieviel Haushalte einnehmen und ausgeben ist stark von der Haushaltsform abhngig. Mehr erwerbsttige Personen im Haushalt bringen ein hheres Einkommen, whrend weitere Haushaltsmitglieder tendenziell auch hhere Konsumausgaben bedeuten. Jedoch zeigt sich, dass insbesondere alleinlebende Personen sowie Erwachsene mit einem oder mehreren Kindern deutlich hhere Kosten tragen als andere Haushaltstypen. Gleichzeitig verfgen sie ber ein erheblich geringeres Einkommen und ein entsprechend niedrigeres <GlossaryTerm className="glossary-term residualIncome" sectionId="residualIncome">Residualeinkommen</GlossaryTerm>. Dies verdeutlicht, dass diese Haushaltsgruppen einem erhhten finanziellen Belastungsrisiko ausgesetzt sind und potenziell grere Schwierigkeiten haben, ihre Lebenshaltungskosten zu decken. Deutliche Unterschiede ergeben sich auch, wenn man ausschlielich Mietshaushalte betrachtet. Insbesondere bei greren Haushalten sinkt das <GlossaryTerm className="glossary-term residualIncome" sectionId="residualIncome">Residualeinkommen</GlossaryTerm> erheblich, was darauf hinweist, dass die Wohnkosten einen mageblichen Einfluss auf die finanzielle Belastung dieser Haushalte haben. Dies zeigt, dass steigende Mietpreise vor allem grere Haushalte berproportional belasten und ihre finanziellen Spielrume erheblich einschrnken knnen.
            </p>
            
            <p className='paragraph'>
                In der Tabelle sieht man die Medianeinkommen, <GlossaryTerm className="glossary-term expenditure" sectionId="residualIncome">notwendigen Ausgaben</GlossaryTerm> und daraus ergebende verfgbare Einkommen fr unterschiedliche Haushaltstypen: 
            </p>

            {/* Houshold income table */}
            <div className="houshold-table-container">
                <HouseholdTable />
            </div>

            <h2 className="content-header">Einkommen und Residualeinkommen</h2>
            <p className='paragraph'>
                Der Scatterplot, der das Haushaltseinkommen und das <GlossaryTerm className="glossary-term residualIncome" sectionId="residualIncome">Residualeinkommen</GlossaryTerm> darstellt, zeigt eine starke positive Korrelation: Haushalte mit hherem Einkommen verfgen in der Regel auch ber ein hheres <GlossaryTerm className="glossary-term residualIncome" sectionId="residualIncome">Residualeinkommen</GlossaryTerm>. Das ist zu erwarten, da hhere Einkommen mehr Spielraum nach Abzug fixer Ausgaben lassen.
            </p>

            <p className='paragraph'>
                Trotz dieser Korrelation ist es wichtig, das <GlossaryTerm className="glossary-term residualIncome" sectionId="residualIncome">Residualeinkommen</GlossaryTerm> separat zu betrachten. Whrend das Haushaltseinkommen allein oft als Mastab fr Wohlstand verwendet wird, zeigt das <GlossaryTerm className="glossary-term residualIncome" sectionId="residualIncome">Residualeinkommen</GlossaryTerm> realistischeren finanziellen Spielraum. Zwei Haushalte mit gleichem Einkommen knnen stark unterschiedliche Lebensrealitten haben  je nach Wohnkosten, Gesundheitsausgaben oder Mobilittsbedarf.
            </p>

            <p className='paragraph'>
                Besonders auffllig sind die Abweichungen von der Korrelation in niedrigen und mittleren Einkommensgruppen. Manche Haushalte haben trotz mittlerem Einkommen ein sehr geringes oder sogar negatives <GlossaryTerm className="glossary-term residualIncome" sectionId="residualIncome">Residualeinkommen</GlossaryTerm>, wenn ihre Fixkosten ihre Einnahmen bersteigen. Das bedeutet, dass sie Schulden machen oder auf Ersparnisse zurckgreifen mssen, um ihren Lebensunterhalt zu bestreiten. Umgekehrt gibt es Haushalte mit relativ niedrigem Einkommen, die aufgrund niedriger Fixkosten dennoch ber ein stabiles <GlossaryTerm className="glossary-term residualIncome" sectionId="residualIncome">Residualeinkommen</GlossaryTerm> verfgen.
            </p>

            <p className='paragraph'>
                Das bedeutet, dass wirtschaftliche Analysen und sozialpolitische Manahmen nicht nur auf das Haushaltseinkommen fokussiert sein sollten. Erst durch die Bercksichtigung des <GlossaryTerm className="glossary-term residualIncome" sectionId="residualIncome">Residualeinkommens</GlossaryTerm> lassen sich tatschliche finanzielle Ungleichheiten und die Notwendigkeit gezielter Untersttzung erkennen.
            </p>
            
            <div className="image-container scatter-plot-container">
                <img src={`${process.env.PUBLIC_URL}/images/3_SCATTER_PLOT_RESI_INCOME_DE.png`} alt="scatter plot visualizing houshold and resiudal income" />
            </div>

            <h2 className="content-header">Ausgaben nach Einkommensquintilen</h2>
            <Box
                sx={{
                    background: "#f4f4f4",
                    padding: 2,
                    borderRadius: 1,
                    boxShadow: 1,
                    marginTop: 3,
                    marginBottom: 3,
                }}
            >
                <div className="toggle-container">
                    <ToggleButtonGroup
                        className="residual-income-toggle quintil-exp-toggle"
                        value={quintilExpenditureMode}
                        exclusive
                        onChange={(_, newValue) => handleToggleModeChange("quintil_exp", newValue)}
                        aria-label="quintil expenditure chart mode selection"
                    >   
                        { /* Old approach
                        <ToggleButton value="all"><label>Allen</label></ToggleButton>
                        <ToggleButton value="renter"><label>Mieter:Innen</label></ToggleButton> */}
                        <ToggleButton value="absolute"><label>Absolut</label></ToggleButton>
                        <ToggleButton value="relative"><label>Relativ</label></ToggleButton>
                    </ToggleButtonGroup>
                </div>
        
                <div className="quintil-exp-charts-container">
                    <QuintilExpenditureCharts 
                        mode={quintilExpenditureMode} 
                        isWrapped={isWrapped}
                        setIsWrapped={setIsWrapped}
                    />
                </div>
            </Box>
            
            <p className='paragraph'>
                Die Grafik zeigt, dass die Arten und die Gesamthhe der Ausgaben bei Haushalten unterschiedlicher Einkommensgruppen auf den ersten Blick recht hnlich erscheinen ({isWrapped ? "oberes Diagramm" : "linke Seite"}). Deutliche Unterschiede zeigen sich jedoch im Verhltnis der Ausgaben zum Einkommen ({isWrapped ? "unteres Diagramm" : "rechte Seite"}).
            </p>

            <p className='paragraph'>
                Die rmsten 20 % der Haushalte geben nahezu ihr gesamtes Einkommen fr <GlossaryTerm className="glossary-term expenditure" sectionId="residualIncome">notwendige Ausgaben</GlossaryTerm> wie Wohnen, Energie oder Lebensmittel aus. Dadurch bleibt ihnen kaum <GlossaryTerm className="glossary-term residualIncome" sectionId="residualIncome">Residualeinkommen</GlossaryTerm> brig, was bedeutet, dass sie kaum Spielraum fr Notflle  wie den Ausfall einer Waschmaschine  oder andere unvorhergesehene Ausgaben haben. Im Gegensatz dazu verwenden die reichsten 20 % der Haushalte nur etwa 30 % ihres Einkommens fr <GlossaryTerm className="glossary-term expenditure" sectionId="residualIncome">notwendige Ausgaben</GlossaryTerm>. Das resultiert in einem sehr hohen <GlossaryTerm className="glossary-term residualIncome" sectionId="residualIncome">Residualeinkommen</GlossaryTerm>, das fr Freizeit, Ersparnisse oder zustzliche Investitionen genutzt werden kann. Bei Haushalten im mittleren Einkommensbereich verbleiben nach den notwendigen Ausgaben 40 bis 60 % ihres Einkommens, wodurch sie zwar mehr Spielraum als die rmsten Haushalte, aber weniger finanzielle Freiheit als die reichsten haben.
            </p>

            <h2>Einkommensungleichheit nach Gesamteinkommen und verfgbaren Einkommen</h2>
            <p className='paragraph'>
                Ein zentrales Ma zur Erfassung der <GlossaryTerm className="glossary-term incomeInequality" sectionId="residualIncome">Einkommensungleichheit</GlossaryTerm> ist der <GlossaryTerm className="glossary-term gini" sectionId="residualIncome">Gini-Koeffizient</GlossaryTerm>. Dieser Wert liegt zwischen 0 und 1, wobei 0 fr eine vllig gleichmige Verteilung (alle Haushalte haben das gleiche Einkommen) und 1 fr maximale Ungleichheit (eine Person erhlt das gesamte Einkommen) steht.
            </p>

            <p className='paragraph'>
                Unsere Analyse zeigt, dass der <GlossaryTerm className="glossary-term gini" sectionId="residualIncome">Gini-Koeffizient</GlossaryTerm> fr das <GlossaryTerm className="glossary-term residualIncome" sectionId="residualIncome">Residualeinkommen</GlossaryTerm> deutlich hher ist als fr das Haushaltseinkommen. Das liegt daran, dass fixe Grundkosten wie Miete, Lebensmittel oder Gesundheitsausgaben einen viel greren Anteil des Einkommens in unteren Einkommensgruppen ausmachen. Dadurch bleibt in diesen Gruppen deutlich weniger finanzielle Flexibilitt, whrend hhere Einkommensgruppen nach den Grundausgaben berproportional mehr Mittel zur Verfgung haben. Dies verstrkt die wirtschaftliche Ungleichheit und macht deutlich, dass das Haushaltseinkommen allein nicht ausreicht, um die (Un-)Gleichverteilung von Ressourcen abzubilden.
            </p>

            {/* GINI counter animation*/}
            <h3 className="gini-header">Gini-Koeffizienten</h3>
            <div className="gini-container">
                <div>
                    <CounterAnimation targetValue={0.27} />
                    <h3 className="gini-subheader">Haushaltseinkommen</h3>
                </div>
                    <div>
                    <CounterAnimation targetValue={0.38} />
                    <h3 className="gini-subheader">Residualeinkommen</h3>
                </div>
            </div>

            <div style={{margin: '24px 0 24px'}}>
                <Accordion sx={{background: "#f4f4f4"}}>
                <AccordionSummary expandIcon={<ExpandMoreIcon />}>
                    <Typography variant="h4" sx={{ fontSize: '1.125rem'}}>Wie wird der Gini berechnet?</Typography>
                </AccordionSummary>
                <AccordionDetails>
                    <Typography>
                        Der Gini-Koeffizient wird aus der Lorenz-Kurve abgeleitet. Diese Kurve zeigt, wie das Einkommen in einer Gesellschaft verteilt ist. Auf der x-Achse steht der Anteil der Haushalte (von den rmsten bis zu den reichsten), auf der y-Achse der Anteil am gesamten Einkommen.
                    </Typography>
                    <ul className="custom-list infobox-list">
                        <li>Schritt 1: Wir ordnen alle Haushalte nach Einkommen, von niedrig nach hoch.</li>
                        <li>Schritt 2: Dann berechnen wir, wie viel Prozent des gesamten Einkommens auf die rmsten 10 %, 20 %, 30 % usw. entfallen.</li>
                        <li>Schritt 3: Wir zeichnen die Lorenz-Kurve: Eine Linie, die zeigt, wie viel Einkommen die unteren Einkommensgruppen zusammen haben.</li>
                        <li>Schritt 4: Der Gini-Koeffizient ergibt sich aus der Flche zwischen der Lorenz-Kurve und der Gleichverteilungslinie (die eine vollkommen gerechte Verteilung darstellen wrde). Je grer diese Flche, desto ungleicher ist die Verteilung.
                            Ein Gini-Koeffizient von 0 bedeutet, dass alle Haushalte exakt das gleiche Einkommen haben. Ein Wert nahe 1 bedeutet, dass fast das gesamte Einkommen von wenigen Haushalten verdient wird.
                        </li>
                    </ul>
                    
                    <div className="image-container lorenz-curve--container">
                        <img className="lorenz-curve" src={`${process.env.PUBLIC_URL}/images/Lorenz_Kurven_Gini_AT.png`} alt="Lorenz curve of Gini Index in AT" />
                    </div>

                </AccordionDetails>
                </Accordion>
            </div>

            <h2 className="content-header">Bildung und ihr Einfluss auf das Residualeinkommen von Haushalten</h2>
            <p className='paragraph'>
                Da die Einkommen stark vom Bildungsniveau, dem Beschftigungsgrad und der Art der Erwerbsttigkeit abhngen, die <GlossaryTerm className="glossary-term expenditure" sectionId="residualIncome">notwendige Ausgaben</GlossaryTerm> aber von allen gettigt werden mssen, spiegeln sich diese wirtschaftlichen Unterschiede noch strker im verfgbaren Einkommen wider. In der Grafik kann zwischen den Kategorien gewechselt werden: 
            </p>

            {/*Education Barcharts*/}
            <EducationCharts />

            <p className='paragraph'>
                Hhere Bildungsabschlsse und Vollzeiterwerbsttigkeit geben den Haushalten deutlich mehr finanziellen Spielraum. Die Bedeutung von Bildung geht weit ber den Abschluss einer Schule oder Universitt hinaus  sie beeinflusst direkt die finanzielle Sicherheit eines Haushalts und die Hhe des <GlossaryTerm className="glossary-term residualIncome" sectionId="residualIncome">Residualeinkommens</GlossaryTerm>.
            </p>

            <p className='paragraph'>
                Fr Haushalte, in denen alle Mitglieder im erwerbsfhigen Alter sind und auf dem Arbeitsmarkt aktiv, aber lediglich ber den hchsten Bildungsabschluss der Pflichtschule verfgen, liegt das <GlossaryTerm className="glossary-term residualIncome" sectionId="residualIncome">Residualeinkommen</GlossaryTerm> bei nur etwa 1190 Euro im <GlossaryTerm className="glossary-term median" sectionId="residualIncome">Median</GlossaryTerm>. Dieser Betrag reicht oft nur fr das tgliche Leben und lsst wenig Raum fr grere finanzielle Spielrume. Die Ursache liegt darin, dass geringere Bildungsabschlsse in der Regel mit niedrigeren Lhnen und weniger flexiblen Arbeitsmglichkeiten verbunden sind, was die Einkommenspotenziale stark begrenzt.
            </p>

            <p className='paragraph'>
                Im Gegensatz dazu zeigt sich bei Haushalten, deren hchste Bildungsabschlsse eine tertire oder postsekundre Ausbildung umfassen, ein deutlich anderes Bild. In diesen Haushalten liegt das Medianeinkommen bei rund 2640 Euro, was fast doppelt so hoch ist. Der Grund dafr ist oftmals, dass hhere Bildungsabschlsse Zugang zu besser bezahlten und stabileren Arbeitspltzen erffnen. Menschen mit hherer Bildung knnen in anspruchsvolleren, qualifizierteren Berufen arbeiten, die nicht nur ein hheres Gehalt bieten, sondern auch bessere Aufstiegschancen und eine grere berufliche Sicherheit.
            </p>

            <p className='paragraph'>
                Die Auswirkungen sind nicht nur individuell sprbar  sie betreffen auch die gesamte Familie. Hheres <GlossaryTerm className="glossary-term residualIncome" sectionId="residualIncome">Residualeinkommen</GlossaryTerm> bedeutet mehr finanzielle Freiheit, die nicht nur fr den Lebensunterhalt genutzt werden kann, sondern auch fr Investitionen in die Zukunft, sei es durch Ersparnisse, die Untersttzung der Ausbildung der Kinder oder die Planung einer stabilen Altersvorsorge.
            </p>

            <p className='paragraph'>
                Jedoch zeigt die Analyse auch eine dramatische Verschiebung, wenn in einem Haushalt zumindest ein Mitglied pensioniert ist. Diese Haushalte haben hufig ein signifikant niedrigeres <GlossaryTerm className="glossary-term residualIncome" sectionId="residualIncome">Residualeinkommen</GlossaryTerm>, da Renten oft nicht ausreichen, um den Lebensstandard zu halten, besonders wenn sie nicht durch private Ersparnisse oder Zusatzgewinne ergnzt werden. Hier wird der Unterschied zwischen Haushalten mit hherer Bildung und denen ohne noch klarer. Oft mssen Pensionisten auf staatliche Untersttzung angewiesen sein, whrend Haushalte mit einem stabilen Einkommensniveau und einer hheren Bildung in der Lage sind, langfristige finanzielle Sicherheit zu gewhrleisten.
            </p>
            
            <p className='paragraph'>
                hnliches zeigt sich, wenn man das Ausma der Beschftigung betrachtet. In Haushalten, in denen der hchste Bildungsabschluss lediglich die Pflichtschule ist, und in denen alle Haushaltsmitglieder, die dem Arbeitsmarkt zur Verfgung stehen, Vollzeit arbeiten, liegt das <GlossaryTerm className="glossary-term median" sectionId="residualIncome">Median</GlossaryTerm><span style={{fontStyle: 'italic'}}>-</span><GlossaryTerm className="glossary-term residualIncome" sectionId="residualIncome">Residualeinkommen</GlossaryTerm> bei etwa 1100 Euro. Trotz Vollzeitarbeit bleibt das Einkommen in diesen Haushalten relativ niedrig. Ganz anders sieht es in Haushalten aus, in denen zumindest ein Familienmitglied eine postsekundre oder tertire Ausbildung abgeschlossen hat. Auch hier arbeiten alle Mitglieder im erwerbsfhigen Alter Vollzeit, doch das <GlossaryTerm className="glossary-term median" sectionId="residualIncome">Median</GlossaryTerm><span style={{fontStyle: 'italic'}}>-</span><GlossaryTerm className="glossary-term residualIncome" sectionId="residualIncome">Residualeinkommen</GlossaryTerm> liegt mit 2800 Euro fast dreimal so hoch wie in den Haushalten mit Pflichtschulabschluss. Der Unterschied ist auffllig und verdeutlicht, wie stark Bildung den Zugang zu besser bezahlten und stabileren Arbeitspltzen beeinflusst.
            </p>
            
            <p className='paragraph'>
            Selbst bei Teilzeitbeschftigung zeigt sich ein hnlicher Trend. In Haushalten mit tertirer oder postsekundrer Ausbildung haben die Familien im <GlossaryTerm className="glossary-term median" sectionId="residualIncome">Median</GlossaryTerm> etwa 400 Euro mehr <GlossaryTerm className="glossary-term residualIncome" sectionId="residualIncome">Residualeinkommen</GlossaryTerm> als Haushalte, deren hchster Bildungsabschluss die Pflichtschule ist. Obwohl Teilzeitarbeit in vielen Fllen geringere Arbeitszeiten und damit auch geringere Einnahmen bedeutet, bleibt der Unterschied zwischen den Haushalten mit unterschiedlichen Bildungsabschlssen bestehen. Auch hier wird die Bedeutung der Ausbildung deutlich: Hhere Bildungsabschlsse ermglichen Zugang zu besser bezahlten Teilzeitstellen, die sich positiv auf das <GlossaryTerm className="glossary-term residualIncome" sectionId="residualIncome">Residualeinkommen</GlossaryTerm> auswirken.
            </p>
            
            <h2 className="content-header">Regionale Unterschiede</h2>

            <p className='paragraph'>
                Die Tabelle veranschaulicht die Unterschiede in den Kosten fr <GlossaryTerm className="glossary-term expenditure" sectionId="residualIncome">notwendige Ausgaben</GlossaryTerm> sowie im Haushalts- und <GlossaryTerm className="glossary-term residualIncome" sectionId="residualIncome">Residualeinkommen</GlossaryTerm> auf Bundeslandebene. Dabei zeigt sich, dass Haushalte in Wien die mit Abstand hchsten Ausgaben haben, was hauptschlich auf die hohen Wohnkosten zurckzufhren ist. Im Vergleich zu anderen Bundeslndern ist der Anteil an Personen, die in Wohneigentum leben, in Wien deutlich geringer. Zudem weisen Wiener Haushalte gemeinsam mit Krnten das niedrigste Haushalts- und <GlossaryTerm className="glossary-term residualIncome" sectionId="residualIncome">Residualeinkommen</GlossaryTerm> auf. Das hchste <GlossaryTerm className="glossary-term residualIncome" sectionId="residualIncome">Residualeinkommen</GlossaryTerm> hingegen findet sich in Obersterreich und Niedersterreich.
            </p>

            <p className='paragraph'>
                Betrachtet man ausschlielich Mietshaushalte, ergibt sich ein anderes Bild: Haushalte in Wien zahlen im Durchschnitt nicht die hchsten Wohnkosten. Stattdessen liegen die Mietausgaben in Vorarlberg am hchsten, gefolgt von Salzburg. Dies spiegelt sich auch im <GlossaryTerm className="glossary-term residualIncome" sectionId="residualIncome">Residualeinkommen</GlossaryTerm> wider, denn Mieter:innen in Wien verfgen ber ein hheres <GlossaryTerm className="glossary-term residualIncome" sectionId="residualIncome">Residualeinkommen</GlossaryTerm> als jene in Vorarlberg und Salzburg. Die niedrigsten Mietkosten tragen hingegen Haushalte im Burgenland und in Krnten.
            </p>

            {/* Exp_Bund Table */}
            <ExpenditureBundTable />


            {/* MeanMap */}
            <Box 
                className="mean-map-container"
                sx={{
                    background: "#f4f4f4",
                    padding: 2,
                    borderRadius: 1,
                    boxShadow: 1,
                    fontSize: '14px',
                    marginTop: 3,
                    marginBottom: 3,
                }}
            >
                <div className="toggle-container">
                    <ToggleButtonGroup
                        className="residual-income-toggle mean-map-toggle"
                        value={meanMapMode}
                        exclusive
                        onChange={(_, newValue) => handleToggleModeChange("mean_map", newValue)}
                        aria-label="mean map mode selection"
                    >   
                        <ToggleButton value="all"><label>Allen</label></ToggleButton>
                        <ToggleButton value="renter"><label>Mieter:Innen</label></ToggleButton>
                    </ToggleButtonGroup>
                </div>
                <MeanMapChart mode={meanMapMode} />
            </Box>

            <p className='paragraph'>
                Erhebliche Unterschiede zeigen sich, wenn man das <GlossaryTerm className="glossary-term residualIncome" sectionId="residualIncome">Residualeinkommen</GlossaryTerm> nach Bundesland und <GlossaryTerm className="glossary-term communitySizeClass" sectionId="residualIncome">Gemeindegrenklasse</GlossaryTerm> betrachtet. Diese regionalen Unterschiede im <GlossaryTerm className="glossary-term residualIncome" sectionId="residualIncome">Residualeinkommen</GlossaryTerm> sind erheblich und verdeutlichen, wie stark die Einkommensmglichkeiten und Lebensbedingungen in verschiedenen Teilen des Landes variieren. Besonders bemerkenswert ist, dass diese Unterschiede nicht nur auf der Hhe des Einkommens beruhen, sondern auch durch lokale Gegebenheiten wie Lebenshaltungskosten und Immobilienpreise beeinflusst werden.
            </p>

            <p className='paragraph'>
                In Wien, der grten Stadt des Landes, liegt das <GlossaryTerm className="glossary-term median" sectionId="residualIncome">Median</GlossaryTerm><span style={{fontStyle: 'italic'}}>-</span><GlossaryTerm className="glossary-term residualIncome" sectionId="residualIncome">Residualeinkommen</GlossaryTerm> mit rund 1545 Euro am niedrigsten. Dies mag auf den ersten Blick berraschen, da man in einer Grostadt hhere Einkommensmglichkeiten vermuten wrde. Allerdings spielen die hohen Lebenshaltungskosten in der Hauptstadt, insbesondere die Immobilienpreise und Mieten im privaten Mietmarkt, eine entscheidende Rolle.
            </p>

            <p className='paragraph'>
                Im Vergleich dazu zeigen die Bundeslnder Niedersterreich, Obersterreich und Vorarlberg ein deutlich hheres <GlossaryTerm className="glossary-term median" sectionId="residualIncome">Median</GlossaryTerm><span style={{fontStyle: 'italic'}}>-</span><GlossaryTerm className="glossary-term residualIncome" sectionId="residualIncome">Residualeinkommen</GlossaryTerm>. Besonders auffllig ist, dass in den kleineren Gemeinden das hchste <GlossaryTerm className="glossary-term residualIncome" sectionId="residualIncome">Residualeinkommen</GlossaryTerm> erzielt wird. Hier spielt der Eigentumserwerb beim Wohnen eine entscheidende Rolle: In lndlicheren Gebieten ist die Eigentumsrate deutlich hher als in stdtischen Zentren. Dies ermglicht es den Haushalten, gegeben, dass sie keine Kreditrckzahlungen bedienen mssen, mehr von ihrem Einkommen zu sparen und somit ein hheres <GlossaryTerm className="glossary-term residualIncome" sectionId="residualIncome">Residualeinkommen</GlossaryTerm> zu erzielen.
            </p>

            <p className='paragraph'>
                Trotz dieser generellen Tendenzen gibt es auch innerhalb der Bundeslnder Unterschiede. In Obersterreich sind es vor allem kleinere Gemeinden, die ein hheres <GlossaryTerm className="glossary-term residualIncome" sectionId="residualIncome">Residualeinkommen</GlossaryTerm> aufweisen, whrend im Burgenland vor allem Haushalte in Eisenstadt das hchste <GlossaryTerm className="glossary-term residualIncome" sectionId="residualIncome">Residualeinkommen</GlossaryTerm> erzielen.
            </p>

            <p className='paragraph'>
                Betrachtet man ausschlielich Mietshaushalte, wird der erhebliche finanzielle Druck durch die Mietkosten besonders deutlich. Besonders ausgeprgte Unterschiede zeigen sich in kleineren Gemeinden in Niedersterreich, Vorarlberg und Krnten. Diese regionalen Unterschiede implizieren, dass das <GlossaryTerm className="glossary-term residualIncome" sectionId="residualIncome">Residualeinkommen</GlossaryTerm> nicht nur von den Einkommen selbst abhngt, sondern auch stark durch die Lebenshaltungskosten in der jeweiligen Region beeinflusst wird.
            </p>

            <p className='paragraph'>
                Allerdings reicht der Blick auf das  <GlossaryTerm className="glossary-term residualIncome" sectionId="residualIncome">Residualeinkommen</GlossaryTerm> allein nicht aus, um die Lebensqualitt und die tatschliche Wohlstandssituation von Haushalten zu beurteilen. Hufig ist es so, dass in Regionen mit hherem  <GlossaryTerm className="glossary-term residualIncome" sectionId="residualIncome">Residualeinkommen</GlossaryTerm> die Infrastruktur und ffentlichen Dienstleistungen nicht in gleichem Mae ausgebaut sind wie in stdtischen Gebieten. Umfassende Infrastruktur und die Verfgbarkeit ffentlicher Dienstleistungen sind ebenfalls entscheidende Faktoren fr den Lebensstandard. In Regionen mit gnstigen Lebenshaltungskosten, aber schlechterer Infrastruktur, knnte das hhere  <GlossaryTerm className="glossary-term residualIncome" sectionId="residualIncome">Residualeinkommen</GlossaryTerm> zwar finanziellen Spielraum bieten, jedoch nicht die gleichen Lebensbedingungen und Mglichkeiten wie in gut erschlossenen urbanen Regionen.
            </p>

            <p className='paragraph'>
                Fr gewisse Bevlkerungsgruppen ist <GlossaryTerm className="glossary-term si" sectionId="residualIncome">soziale Infrastruktur</GlossaryTerm> besonders wichtig. Vor allem fr Haushalte mit geringem Einkommen oder groer finanzieller Belastung durch Lebenserhaltungskosten ist diese von groer Bedeutung. Diese Gruppen knnen sich hufig keine teuren privaten Lsungen leisten, weshalb sie ffentliche Angebote wie Gesundheitsversorgung, Kinderbetreuung und andere <GlossaryTerm className="glossary-term si" sectionId="residualIncome">soziale Infrastrukturen</GlossaryTerm> deutlich finanziell entlasten knnen.
            </p>

            {/* Infobox with CommunitySizeClass map */}
            <div style={{margin: '24px 0 0'}}>
                <Accordion sx={{background: "#f4f4f4"}}>
                <AccordionSummary expandIcon={<ExpandMoreIcon />}>
                    <Typography variant="h4" sx={{ fontSize: '1.125rem'}}>Wo wohnt sterreich: Gemeindegreklassenerklrung</Typography>
                </AccordionSummary>
                <AccordionDetails>
                    <Typography>
                        In vielen regionalen Statistiken werden die sterreichischen Gemeinden in <GlossaryTerm className="glossary-term communitySizeClass" sectionId="residualIncome">Gemeindegreklassen</GlossaryTerm> eingeteilt. Sechs sterreichische Landeshauptstdte haben ber 100.000 Einwohner:innen (Wien, Graz, Linz, Salzburg, Klagenfurt und Innsbruck). Insgesamt leben etwas mehr als 20% aller Bewohner:innen sterreichs in diesen Stdten, hnlich viele wie in den 1.366 Gemeinden mit weniger als 2.500 Einwohner:innen. Mehr als 30% der sterreicher:innen leben in den 641 Gemeinden mit 2.500-10.000 Einwohner:innen und 27% in Klein- und Mittelstdten.
                    </Typography>

                    <img className="community-size-map" src={`${process.env.PUBLIC_URL}/images/Abb_KarteGemeindegroeen_DE.png`} alt="Gemeindegreklassen" />
                </AccordionDetails>
                </Accordion>
            </div>        


        </div>
    );
};

export default ResidualIncomeSection;